/**
 * 时间转时间戳
 * @param {*} str
 */

export function timeStampToString(str, format = "Y-m-d H:i:s") {
    format = format.toLowerCase();
    
    let formatArr = ["y", "m", "d", "h", "i", "s"];
    
    let timeData = format;
    
    let dateNum = new Date(str * 1000);
    
    formatArr.forEach((item) => {
        
        if (format.search(item) != -1) {
            
            switch (item) {
                
                case "y": {
                    timeData = timeData.replace(eval("/" + item + "/g"), dateNum.getFullYear())
                    break;
                }
                
                case "m": {
                    let MM = dateNum.getMonth() + 1;
                    MM = MM < 10 ? ('0' + MM) : MM;//月补0
                    timeData = timeData.replace(eval("/" + item + "/g"), MM)
                    break;
                }
                
                case "d": {
                    let d = dateNum.getDate();
                    d = d < 10 ? ('0' + d) : d;//天补0
                    timeData = timeData.replace(eval("/" + item + "/g"), d)
                    break;
                }
                
                case "h": {
                    let h = dateNum.getHours();
                    h = h < 10 ? ('0' + h) : h;//小时补0
                    timeData = timeData.replace(eval("/" + item + "/g"), h)
                    break;
                }
                
                case "i": {
                    let m = dateNum.getMinutes();
                    m = m < 10 ? ('0' + m) : m;//分钟补0
                    timeData = timeData.replace(eval("/" + item + "/g"), m)
                    break;
                }
                
                case "s": {
                    let s = dateNum.getSeconds();
                    s = s < 10 ? ('0' + s) : s;//秒补0
                    
                    timeData = timeData.replace(eval("/" + item + "/g"), s)
                    break;
                }
                
                default:
            }
            
        }
    })
    
    return timeData;
}


/**
 * @description 将图片的base64 转变成Blob形式
 * */

export function getImageByCanvas(dataurl) {
    var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
    while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], {type: mime});
}

/**
 * @description 判断平台以及是否为Safari浏览器
 * */
export function getPlatform() {
    
    let native = {};
    let p = navigator.platform;
    let u = navigator.userAgent;
    let is_android = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1;
    let is_ios = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);

    //判断平台
    if (p == "Win32" || p == "Win64" || p == "MacPPC" || p == "MacIntel" || p == "X11" || p == "Linux i686") {//PC调试环境
        native.platform = "pc"
    } else {
        if (is_android) {
            native.platform = "android"
        }else if(is_ios){
            native.platform = "ios"
        } 
    }
    var ua = u.toLowerCase();
    //判断是否为Safari中打开:
    if (ua.indexOf('applewebkit') > -1 && ua.indexOf('mobile') > -1 && ua.indexOf('safari') > -1 &&
            ua.indexOf('linux') === -1 && ua.indexOf('android') === -1 && ua.indexOf('chrome') === -1 &&
            ua.indexOf('ios') === -1 && ua.indexOf('browser') === -1) {
        
        native.isSafari = true
    } else {
        native.isSafari = false
    }
    
    //判断是否为微信中打开:
    if (ua.indexOf('micromessenger') !== -1 || ua.indexOf('weibo') !== -1) {
        native.isWeixin = true
    } else {
        native.isWeixin = false
    }
    
    //判断是否为QQ中打开:
    if (ua.match(/QQ/i)=='qq') {
        native.isQQ = true
    } else {
        native.isQQ = false
    }
    
	// console.log(native)
    return native
}
